.m-page-content {
  /* transform: translateY(-34px); */
  transition-duration: 0s;
}
.pannelwrap {
  padding-top: 0px;
  padding-bottom: 0px;
  width: 100%;
  overflow: hidden;
}
.wb-item-wrap {
  background: #fff;
}
.wb-item-wrap .card9.card {
  margin: 0
}

.f-weibo .m-img-box {
  background-color: #e6e6e6
}

.empty-bg {
  width: 100%;
  background-color: #e6e6e6;
  height: .375rem
}

.inline-block {
  display: inline-block
}

.txt-margin {
  margin: 0 0 1rem 0
}

.width-min {
  width: 4.375rem
}

.anim-load {
  animation: load .5s ease-out;
  -moz-animation: load .5s ease-out;
  -webkit-animation: load .5s ease-out;
  -o-animation: load .5s ease-out
}

@keyframes load {
  0% {
      background-color: #fff
  }

  100% {
      background-color: #e6e6e6
  }
}

@-moz-keyframes load {
  0% {
      background-color: #fff
  }

  100% {
      background-color: #e6e6e6
  }
}

@-webkit-keyframes load {
  0% {
      background-color: #fff
  }

  100% {
      background-color: #e6e6e6
  }
}

.f-more {
  letter-spacing: .1rem
}

.f-weibo .f-card-title {
  margin: -1rem -1rem .5rem -1rem;
  padding: 0 1rem;
  border-width: 0
}

.f-weibo .m-avatar-box .m-img-box .m-icon {
  font-size: 14px
}

.iosx3 .card9 .f-card-title {
  border-width: 0
}

.iosx2 .card9 .f-card-title {
  border-width: 0
}

.f-weibo.card9 {
  border-bottom: 1px solid #e6e6e6
}

.iosx3 .f-weibo.card9 {
  border-bottom: .36px solid #e6e6e6
}

.iosx2 .f-weibo.card9 {
  border-bottom: .5px solid #e6e6e6
}

.f-weibo.card9>.card-wrap {
  margin-left: .75rem;
  margin-right: .75rem
}

.f-weibo.card9.m-panel {
  border-top-width: 0
}

.f-weibo.card .card-wrap .f-col-wrap {
  padding: 0 .9375rem
}

.f-weibo.card9 .m-box-col {
  min-width: 0
}

.f-weibo.card9 .weibo-top {
  padding: 0 0 0 .25rem
}

.f-weibo.card9 .weibo-top .m-box-col .m-icon {
  margin-left: 3px
}

.f-weibo.card9 .weibo-main .weibo-og {
  padding: .75rem 0 0 .25rem
}

.f-weibo.card9 .weibo-main .card-wrap ~ .weibo-rp {
  margin-top: 0.5rem
}

.f-weibo.card9 .weibo-main .media-b {
  margin: .625rem 0 -.375rem
}

.f-weibo.card9 .weibo-main .media-b .m-auto-list {
  margin: 0 0 -.25rem
}

.f-weibo .weibo-top .m-text-box {
  margin: .15rem 0 .15rem .5rem
}

.f-weibo .f-r {
  float: right
}

.f-weibo .weibo-main .weibo-og {
  font-size: .9375rem
}

.f-weibo .weibo-rp .weibo-text {
  font-size: .9375rem
}

.f-weibo .weibo-rp .f-footer-ctrl {
  padding: 0.625rem 0 0
}

.f-weibo .f-bg-img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  width: 100%;
  height: 100%
}

.f-footer-ctrl {
  border-top-width: 0;
  height: 1.1rem;
  padding-bottom: 1.5rem;
  padding-top: 1rem;
  margin: 0 0.75rem
}

.f-footer-ctrl .m-diy-btn {
  color: rgba(40,47,60,0.8);
  /* height: 100%; */
  float: left
}

.f-footer-ctrl .m-diy-btn+.m-diy-btn {
  margin-left: 1.6875rem
}

.f-footer-ctrl .m-diy-btn .m-icon {
  font-size: 16px
}

.f-footer-ctrl aside {
  float: right;
  color: rgba(40,47,60,0.8)
}

.f-footer-ctrl .m-font {
  font-size: 1rem;
  vertical-align: middle
}

.f-footer-ctrl .m-diy-btn h4 {
  font-size: .8125rem;
  display: inline-block;
  margin-top: 0;
  margin-left: .25rem
}

.m-auto-list li{
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  width: 33.33%;
  padding: 0 .3125rem .3125rem 0;
}

.m-auto-list li .m-img-box-list {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  position: absolute;
  width: 100%;
  height: 100%;
  vertical-align: top;
  object-fit: cover;
}
.m-img-box {
  position: relative;
  padding-bottom: 100%;
}